.card-cont{
    height: 40vh;
}

.proj-card{
    cursor: pointer;
    /* width: 100%;
    height: auto; */
    /* width: 30vw;
    max-height: 100%; */
    /* height: 75vh; */
}
/* .proj-img{
    max-width: 100%;
    
    position: relative;
} */
.front, .back{
    width: 100%;
    height: 100%; 
    overflow: hidden;
    backface-visibility: hidden;
    position: absolute;
    transition: transform .6s;
}

.front img{
    height: 100%;
}

.front{
    transform: perspective(600px) rotateY(0deg);
}
.back{
    background: #4595a4;
    opacity: 1;
    transform: perspective(600px) rotateY(180deg);
    max-width: 94%;
}

.proj-desc{    
    width: 100%;
    font-size: 0.7em;
    color: #f2f2ef;
}

.proj-desc .title{
    color: #ffdd3e;
    text-align: center;
    margin-top: 10%;
    margin-bottom: 5%;
    font-weight: 700;
}

.proj-desc .descript{
    text-align: justify;
    margin: 5%;
}

.proj-desc .tech{
    text-align: justify;
    margin: 5%;
}
.sm{
    margin: 20px 0;
    overflow: hidden;
    margin: 5%;
}

.sm p{
    display: inline-flex;
    /* width: 40px;
    height: 40px; */
    justify-content: center;
    align-items: center;
    /* color: #f2f2ef; */
    transition: 1s;
    
}
.logoLink {
    position: absolute;
    bottom: 1px;
    right: 2px;
}

.proj-card:hover > .front{
    transform: perspective(600px) rotateY(-180deg);
}
.proj-card:hover > .back{
    transform: perspective(600px) rotateY(0deg);
}
